import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/index'
import ChatRoom from '../views/chat'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/chats',
        name: 'ChatRoom',
        component: ChatRoom
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
